<template>
  <div class="All">

<!--    顶部栏-->
    <div class="TopBox">
      <div class="TitleBox">文本翻译</div>
    </div>

<!--    所有信息栏-->
    <div class="MiddleBox" ref="scrollArea">
      <div class="DataBox"
           v-for="(item, index) in infoList"
           :key="index"
           style="overflow-y: scroll;">

        <div class="DataItemBox"
             :class="{ 'align-right': item.id === 1, 'align-left': item.id === 2 }"
        >
          <div :class="{ 'DataItem1': item.id === 1, 'DataItem2': item.id === 2 }"
          >{{item.data}}</div>
        </div>

      </div>
    </div>

<!--    <div style="height: 1000px; background-color:#797979;"></div>-->

    <!--    按住说话按钮-->
    <div class="speakBigBox">

<!--      文本框-->
      <textarea class="speakText"
                v-if="istext"
                v-model="text">

      </textarea>

      <div class="speakBox_Two">
        <div class="jianPan"
             :style="istext ? { backgroundColor: '#4fabe5' } : { backgroundColor: 'gray' }"
             @touchstart.stop="pressJpButton"
             @touchend.stop="releaseJpButton"
             :class="{ 'small': isJpPressed }"
             ontouchstart="return false;"
             onselectstart="return false;">
          <i class="iconfont icon-jianpan1"></i>
        </div>
        <div class="SpeakBox"
             @touchstart.stop="pressButton"
             @touchend.stop="releaseButton"
             :class="{ 'small': isPressed }"
             ontouchstart="return false;"
             onselectstart="return false;"
        >
          <i class="ht iconfont icon-huatong"></i>
          按住说话
        </div>
        <div class="faSong"
             :style="text ? { backgroundColor: '#4fabe5' } : { backgroundColor: 'gray' }"
             @touchstart.stop="pressFsButton"
             @touchend.stop="releaseFsButton"
             :class="{ 'small': isFsPressed }"
             ontouchstart="return false;"
             onselectstart="return false;">
          <i class="iconfont icon-fasong"></i>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import axios from "axios";
import Vue from "vue";

export default {
  name: 'HomeView',
  data() {
    return {
      isPressed: false,
      isJpPressed: false,
      isFsPressed: false,
      istext: false,
      text: "",
      recognition: null,
      infoList: []
    }
  },
  mounted() {
    this.getCachedData();
    this.scrollToBottom();
  },

  methods: {
    scrollToBottom() {
      Vue.nextTick(() => {
        this.$refs.scrollArea.scrollTop = this.$refs.scrollArea.scrollHeight;
      });
    },
    getCachedData() {
      // 从 localStorage 获取数据
      const cachedInfoList = localStorage.getItem('infoList');

      // 检查是否存在缓存数据
      if (cachedInfoList) {
        // 将 JSON 字符串解析为对象
        this.$data.infoList = JSON.parse(cachedInfoList);
        console.log(this.$data.infoList)

      } else {
        console.log('没有找到缓存数据');
      }
    },
    pressButton() {
      if (!this.istext) {
        this.istext = true
      }
      this.isPressed = true;
      // this.istext = true;
      this.recognition = new window.webkitSpeechRecognition(); // 使用 WebkitSpeechRecognition，在某些浏览器中可能需要前缀
      this.recognition.lang = 'zh-CN'; // 设置识别语言为中文
      this.recognition.continuous = true; // 开启连续识别
      this.recognition.onresult = (event) => {
        this.$data.text = event.results[event.results.length - 1][0].transcript;
        for (let i = 0; i < event.results.length; i++) {
          console.log(event.results[i][0].transcript);
        }
      };
      this.recognition.start();
    },

    releaseButton() {
      if (this.recognition) {
        this.recognition.stop();
        this.recognition = null;
      }
      // if (this.text === "") {
      //   this.istext = false;
      // }
      this.isPressed = false;
    },
    pressJpButton() {
      this.isJpPressed = true;
    },
    releaseJpButton() {
      // this.istext = true;
      this.isJpPressed = false;
      this.istext = !this.istext;
    },
    pressFsButton() {
      if (this.text) {
        this.isFsPressed = true;
      }
    },
    releaseFsButton() {
      if (this.text) {
        this.isFsPressed = false;
        this.handleTranslate();
      }
    },
    handleTranslate() {

      const dataitem = this.$data.text;
      const newItem = {
        id: 1,
        data: dataitem
      };
      this.$data.text = "";
      this.istext = false;
      this.scrollToBottom()
      // 从 localStorage 获取现有的 infoList 数组
      let infoList = JSON.parse(localStorage.getItem('infoList')) || [];

      // 将新的数据对象添加到数组中
      infoList.push(newItem);

      // 将更新后的数组存回 localStorage
      localStorage.setItem('infoList', JSON.stringify(infoList));
      this.getCachedData()
      axios.post(
          "https://aip.baidubce.com/rpc/2.0/mt/texttrans/v1?access_token=24.8f9127728ff2e7ac7ff43a33eb40db88.2592000.1720589798.282335-80733545",
          {
            // 这里放请求主体的数据
            // 例如：
            q: dataitem,
            from: "zh",
            to: "en"
          },
          {
            headers: {
              "Content-Type": "application/json;charset=utf-8"
            }
          }
      )
          .then(res => {
            const newItem = {
              id: 2,
              data: res.data.result.trans_result[0].dst
            };

            // 从 localStorage 获取现有的 infoList 数组
            let infoList = JSON.parse(localStorage.getItem('infoList')) || [];

            // 将新的数据对象添加到数组中
            infoList.push(newItem);

            // 将更新后的数组存回 localStorage
            localStorage.setItem('infoList', JSON.stringify(infoList));
            this.getCachedData()
            this.scrollToBottom()

          })
          .catch(error => {
            console.error(error);
          });

    }
  }
}
</script>

